import React from 'react';
import { FormControl, TablePagination } from '@material-ui/core';
import { css, ClassNames } from '@emotion/core';
import SelectField from 'common/inputs/formik/SelectField';
import { Direction } from 'utils/query';
import { useContainer } from 'unstated-next';
import { useFormikContext } from 'formik';
import { useDebounceFormikSubmit } from 'utils/hooks';
import { EntryContainer } from './hooks/entry';
import { usePluginContainer } from './hooks/api';
const wrapper = (theme) => css `
  display: flex;
  justify-content: space-between;
  padding-bottom: ${theme.typography.pxToRem(theme.spacing(2))};
  align-items: center;
`;
const container = css `
  display: flex;
  align-items: center;
  font-size: 14px;
`;
const item = (theme) => css `
  margin-left: ${theme.typography.pxToRem(theme.spacing(2))};
  margin-right: ${theme.typography.pxToRem(theme.spacing(2))};
`;
const input = css `
  font-size: inherit;
`;
const sortOrderOptions = [
    {
        value: Direction.Desc,
        label: 'Desc',
    },
    {
        value: Direction.Asc,
        label: 'Asc',
    },
];
const EntryListHeader = ({ options: { page, perPage } }) => {
    const [{ totalCount }] = useContainer(EntryContainer);
    const { sortByOptions } = usePluginContainer();
    const { setFieldValue } = useFormikContext();
    const handleChangePerPage = (event) => {
        setFieldValue('perPage', parseInt(event.target.value, 10));
        setFieldValue('page', 0);
    };
    const handleChangePage = (_, p) => {
        setFieldValue('page', p);
    };
    useDebounceFormikSubmit();
    return (<ClassNames>
      {({ css: cssString }) => (<div css={wrapper}>
          <div css={container}/>
          <div css={container}>
            <FormControl css={item}>
              <SelectField label="Sort By" name="sortBy" id="sortBy" size="small" InputProps={{ className: cssString(input) }} options={sortByOptions}/>
            </FormControl>
            <FormControl css={item}>
              <SelectField label="Order" name="order" id="order" size="small" InputProps={{ className: cssString(input) }} options={sortOrderOptions}/>
            </FormControl>

            <TablePagination rowsPerPageOptions={[30, 60, 90]} count={totalCount} rowsPerPage={perPage} page={page} onChangePage={handleChangePage} onChangeRowsPerPage={handleChangePerPage} component="div"/>
          </div>
        </div>)}
    </ClassNames>);
};
export default EntryListHeader;
